.modal {
  background: white;
  color: black;
  border: 3px solid #BA4585;
  border-radius: .3rem;
  margin: 100px auto;
  max-width: 400px;
}
.modal * {
  text-transform: none !important;
}

.modal .button {
	background-color: #406FAF;
	border-radius: .3rem;
	border: 1px solid transparent;
	cursor: pointer;
	font-size: 1.25rem;
	line-height: 1.5;
	padding: .5rem 1rem;
	margin-top: 1rem;
}

.modal a, .modal a:visited {
	color: #82609B;
}

.modal h1 {
	margin: 0;
}

.ReactModal__Overlay {
  z-index: 20;
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.rules {
  font-size: 1.2rem;
  line-height: 1.5;
}

.rules li {
  list-style: none;
}

.rules * {
	text-transform: none;
}

.rules h1 {
	text-transform: uppercase;
}

.rules a, .rules a:visited {
	color: #82609B;
}

.scoreboard {
	width: 100%;
}

.scoreboard * {
	text-transform: none !important;
}

.overall-leaders * {
	text-transform: none !important;
}

table.scoreboard, .overall-leaders  {
	border-spacing: 0;
	border-collapse: collapse;
}


.scoreboard td, .overall-leaders td {
  border-bottom: 1px solid grey;
  padding: 5px;
  font-size: 1.3rem;
}

.scoreboard th, .overall-leaders th {
  border-bottom: 2px solid;
  padding: 5px;
  text-align: left;
}

.toTeam {
  cursor: pointer;
}

.section-title {
  -ms-flex-align: center;
      align-items: center;
  color: #9f3942;
  display: -ms-inline-flexbox;
  display: inline-flex;
  font-size: 2.25rem;
  font-weight: bold;
  margin-bottom: 0;
  text-transform: capitalize;
}

table.solves {
	border-spacing: 0;
	border-collapse: collapse;
}

table.solves * {
	text-transform: none !important;
}

.solves {
	table-layout: fixed;
	margin-top: 4em;
}

.solves thead tr {
	height: 4rem;
}

.solves th {
	-webkit-transform: rotate(-50deg);
	-moz-transform: rotate(-50deg);
	-ms-transform: rotate(-50deg);
	-o-transform: rotate(-50deg);
}

.solves th:first-child {
	width: 10em;
	-webkit-transform: unset;
	-moz-transform: unset;
	-ms-transform: unset;
	-o-transform: unset
}

.solves th, .solves td {
	text-align: center;
}

.solves .solved {
    color: #739F75;
    font-size: 1.6em;
}

#leaderboard {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: stretch;
	    align-items: stretch;
	width: 100%;
	margin-right: 3rem;
}

.overall-leaderboard {
	margin-right: .5rem;
}

.race-log {
	margin-left: .5rem;
	margin-right: .5rem;
}

.race-list {
	margin-left: .5rem;
}

#leaderboard h2 {
	background-color: #82609B;
	border-radius: 1.5rem;
	padding-left: 1.5rem;
	padding-right: 1.5rem;
}

.race-log-element {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column-reverse;
	    flex-direction: column-reverse;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: stretch;
	    align-items: stretch;
}

.race-log-element:hover {
	-webkit-filter: brightness(150%);
	        filter: brightness(150%);
}

.race-name {
	text-align: left;
}
.race-open {
	text-align: right;
}


@media screen and (max-width: 988px) {
	html {
		font-size: 90%;
	}
}




